import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CepBairro, CLASS_NAME } from '../../../../models/dtos/ger/cepBairro';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';

interface CepBairroEditViewProps extends EditViewProps<CepBairro> {}

const CepBairroEditView: FC<CepBairroEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CepBairro>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Bairro">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <CepLocalidadeIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="Localidade"
              placeholder="Selecione uma Cidade"
              id="localidade"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="bairro"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              label="Nome"
              placeholder="Informe o Nome"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CepBairroEditView;
